import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

export default class Fashion extends React.Component {
    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title></title>
                    <link rel="canonical" href="https://www.ezyvue.com/industries/fashion" />
                </Helmet>
                <section className="full-width-viewer-header">
                    <div>
                        <div>
                            <h1>3D product visualization for fashion retailers and manufacturers</h1>
                            <h3>Showcase your product range online in 3D and AR with the Ezyvue 3D Viewer</h3>
                        </div>
                    </div>
                    <div>
                        <div className='homepage-viewer viewer-outer'>
                            <div className='aspect-inner'>
                                <iframe id="ezyvue-viewer"
                                    src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=63c391734560352a23948844&hidemenu`}
                                    width="100%"
                                    height="100%"
                                    style={{ border: 'none' }}
                                    frameBorder="0"
                                    allow="xr-spatial-tracking"
                                    allowFullScreen={true}
                                    mozallowfullscreen="true"
                                    webkitallowfullscreen="true" />
                            </div>
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className="flexcol">
                            <h2>Let your customers view all your product variants</h2>
                            <p>
                                Customers can choose from all the available options including materials, fabrics and component parts.<br />
                                Using app-less WebAR, Android users can try out the options in their home.
                            </p>
                        </div>
                        <div className='flexcol'>
                            <img style={{ opacity: '90%', width: '400px' }} className='homepage-section-img' alt='3d animations'
                                src={require('../../assets/images/tshirtvariants.jpg')} loading='lazy' />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>The benefits of 3d visualization in the fashion industry</h2>
                            <p>
                                Sharing your pre-production digital designs within your organization and customers can speed up the production process and reduce costs and time to market.
                            </p>
                        </div>
                        <div className="flexcol">
                            <img className='homepage-section-img'
                                alt='Augmented reality for iOS, Android and WebAR with product personalization' src={require('../../assets/images/jacket.jpg')} />
                        </div>
                    </div>
                    <div className="info-boxes-container">
                        <div className='info-box' style={{ fontWeight: 'bold', height: '200px' }}>
                            <div className="info-box-outer">
                                <div className="info-box-inner">
                                    Share digital designs within your company or with customers during the pre-production cycle
                                </div>
                            </div>
                        </div>
                        <div className='info-box' style={{ fontWeight: 'bold', height: '200px'  }}>
                            <div className="info-box-outer">
                                <div className="info-box-inner">
                                    Reduce the number of physical samples that have to be made and set to customers and reduce waste
                                </div>
                            </div>
                        </div>
                        <div className='info-box' style={{ fontWeight: 'bold', height: '200px'  }}>
                            <div className="info-box-outer">
                                <div className="info-box-inner">
                                    Reduce time to market and development costs
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>Bring a unique shopping experience to your fashion business with AR</h2>
                            <p>
                                AR provides a unique way to showcase your brand and keep customers engaged while shopping.

                            </p>
                        </div>
                        <div className='flexcol'>
                            <img className='homepage-section-img' style={{ width: '300px' }} alt='augmented reality apps' src={require('../../assets/images/webar.jpg')} />
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}